<template>
  <div class="goods">
    <div>
      <ul class="cl">
        <li>
          <router-link to="/recommend">
            <span>热门推荐</span>
          </router-link>
        </li>
        <li>
          <router-link to="/storage" class="exact">
            <span>存号箱</span>
          </router-link>
        </li>
      </ul>
    </div>
    <van-list v-model="loading"
              :finished="finished"
              finished-text="没有更多了"
              @load="onLoad"
              class="van-clearfix">
      <van-cell class="box" v-for="item in list" :key="item.awardId">
        <div class="top">
          <div class="icon">
            <img :src="item.icon" />
          </div>
          <div class="game_name">
            <span class="name">{{item.pagName}}</span>
          </div>
          <div class="get_gift">
            <img class="checked" src="../../asset/received_icon.png" alt />
            <span>已领取</span>
          </div>
        </div>
        <div class="description">
          <div class="left">
            <div class="gift_name">
              <p>{{item.awardName}}</p>
              <p>{{item.awardRemark}}</p>
            </div>
            <div class="cdk">
              <span>激活码</span>
              <span class="cdkData">{{item.awardCode}}</span>
            </div>
          </div>

          <div class="copy">
            <div class="copy_btn" @click="createCopy()" :data-clipboard-text="item.awardCode">
              <img src="../../asset/copy_btn.png" alt />
            </div>
          </div>
        </div>
      </van-cell>
    </van-list>
  </div>
</template>

<script>
import { Cell, List, Toast } from "vant";
import axios from "axios";
import IndexLess from "./index.less";
import { getCookie } from "@/untils/cookie";

export default {
  components: {
    [List.name]: List,
    [Cell.name]: Cell
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
        page:1
    };
  },
  methods: {
    onLoad() {
        // var phone = 13798163108;
        this.loading = true;
      axios
        .post(this.VUE_APP_API + "queryCaseNumList.do", {
          activityId: 190822080,
          account:getCookie('phone'),
          page:this.page++,
            rows:5
        })
          .then(response => {
              console.log("response", this.list);
              if (this.list.length == 0) {
                  this.list = response.data.data.list;
              } else {
                  response.data.data.list.forEach((item, index, array) => {
                      this.list.push(item);
                  });
              }
              // 加载状态结束
              this.loading = false;
              // 数据全部加载完成
              console.log("12", response.data.data.counts);
              if (this.list.length >= response.data.data.counts) {
                  this.finished = true;
              }
          })
        .catch(function(error) {
          console.log(error);
        });
    },
    createCopy() {
      let clipboard = new this.Clipboard(".copy_btn"); //此处为点击的dom的类名
      clipboard.on("success", e => {
        Toast({
          message: "激活码已复制",
          position: "center",
          duration: 1000
        });
        // 释放内存
        clipboard.destroy();
      });
      clipboard.on("error", e => {
        // 不支持复制
        Toast({
          message: "复制失败,请手动复制",
          position: "center",
          duration: 1000
        });
        // 释放内存
        clipboard.destroy();
      });
    }
  }
};
</script>
<style lang="less" scoped>
.van-cell:not(:last-child)::after {
  right: inherit;
}
.box:first-child {
  padding-top: 12px !important;
  height: 180px;
}
.box {
  width: 100%;
  height: 172px;
  padding: 4px 12px !important;
  .van-cell__value.van-cell__value--alone {
    background: url("../../asset/box.png");
    background-size: cover;
    background-clip: padding-box;
    background-origin: content-box;
    /*background-position: 2px 0px;*/
  }

  .top {
    display: flex;
    box-sizing: border-box;
    height: 60px;
    line-height: 30px;
    padding: 15px 12px;
    .icon {
      width: 30px;
      margin-right: 9px;
      img {
        width: 30px;
        height: 30px;
      }
    }

    .game_name {
      flex: 1;
      .name {
        font-size: 15px;
        color: rgb(78, 78, 78);
        font-family: "PingFang";
      }
    }
    .get_gift {
      width: 67px;

      padding-right: 12px;
      .checked {
        width: 16px;
        height: 16px;
        margin-right: 6px;
        vertical-align: middle;
      }
      span {
        color: #000000;
        font-size: 15px;
        vertical-align: middle;
      }
    }
  }
  .description {
    box-sizing: border-box;
    padding: 14px 12px;
    height: 104px;
    display: flex;
    .left {
      flex: 1;
      .gift_name {
        margin-bottom: 11px;
        p:first-child {
          height: 16px;
          line-height: 16px;
          margin: 0 0 5px 0px;
          font-size: 15px;
        }
        p:last-child {
          height: 13px;
          line-height: 13px;
          margin: 0;
          font-size: 10px;
          font-family: "PingFang";
          color: rgb(78, 78, 78);
        }
      }
      .cdk {
        span {
          float: left;
          font-size: 14px;
          font-family: "PingFang";
          height: 29px;
          line-height: 29px;
        }
        .cdkData {
          text-align: center;
          font-size: 14px;
          min-width: 120px;
          padding: 0 5px;
          background-color: rgb(241, 241, 241);
          border-radius: 15px;
          margin-left: 12px;
        }
      }
    }
    .copy {
      width: 66px;
      margin: 23px auto 20px auto;
      .copy_btn {
        width: 66px;
        height: 27px;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
</style>